#root {
    margin:0 auto;
    
}


.main {
    width: 100%;
    margin: 0 auto;
    display: flex;
    float: right;
}

.part {
    margin: 0 auto;
    width: 680px;
    height: 350px;
    margin-left: 90px;
    margin-top: 15px;
    margin-bottom: 5px;
    background-color: rgba(201, 201, 201, 0.445);
    display: flex;
    float: left;
    border-radius: 5px;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
    
}

.img {
    width: 100%;
    margin-right: 30px;
    max-width: 150px;
}

.img img {
    object-fit: cover;
    margin-bottom: 50px;
    top: -50px;
    
    
}

.title {
    margin-left: 10px;
}

.title h2 {
    color: rgb(255, 255, 255);
}

.title p {
    height: 170px;
    overflow: auto ;
}

.title::-webkit-scrollbar  {
    width : 10px;
    background-color: rgba(2, 2, 2, 0.212);
}

.genre ul{
    display: flex;
    margin-left: -15px;
}

.genre li{
    margin-right: 25px;
    
}